<script>
  import { getDailyZbon } from '@/components/Utlis/api'
  import { formatDate } from '@/common/common'
  import moment from 'moment'
  import IKUtils from 'innerken-js-utils'

  export default {
    props: {
      id: {
        default: -1,
        type: Number,
      },
    },
    data: function () {
      return {
        finishZbonTime: '',
        dates: [],
        taxInfos: [],
        taxItem: {
          consumeTypeName: '',
          taxGroupTotal: 0,
          taxRateGroupValue: 0,
        },
        isAllPrint: false,
        menu: false,
        loading: false,
        currentIndex: 0,
        servantId: this.id,
        servantData: {},
        infoList: [],
        search: '',
      }
    },
    computed: {
      todayDate: function () {
        if (Date.parse(new Date()) > Date.parse(this.finishZbonTime)) {
          return moment(new Date()).add(-1, 'days').format('YYYY-MM-DD')
        } else {
          return moment(new Date()).add(-2, 'days').format('YYYY-MM-DD')
        }
      },
    },
    watch: {
      dates: {
        immediate: true,
        handler: function () {
          this.reviewZbon()
        },
      },
    },
    mounted () {
      this.finishZbonTime = moment().format('YYYY-MM-DD') + ' 04:00:00'
      if (Date.parse(new Date()) > Date.parse(this.finishZbonTime)) {
        this.dates = [formatDate(-1), formatDate(-1)]
      } else {
        this.dates = [formatDate(-2), formatDate(-2)]
      }
    },
    methods: {
      clear () {
        this.dates = []
      },
      goBack () {
        this.$router.history.go(-1)
        // this.$router.push({ name: '桌子管理' })
      },
      async reviewZbon () {
        if (this.dates.length > 0) {
          if (this.dates.length === 1) {
            this.dates[1] = this.dates[0]
          }
          if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
            [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
          }
          this.taxInfos = []
          await getDailyZbon(this.dates).then(res => {
            res.content.forEach(item => {
              this.taxItem = IKUtils.deepCopy(item)
              this.taxInfos.push(this.taxItem)
            })
            console.log(this.taxInfos, 'this.taxInfos')
          })
        }
      },
    },
  }
</script>

